
import {map} from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { Response } from '@angular/http';
import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Observable } from 'rxjs';
import { CookieService } from "angular2-cookie/core";


@Injectable()
export class ClinicService {

   public token: string;
   public api_url: string;
    private headers: HttpHeaders;
    constructor(private http: HttpClient,private _cookieService: CookieService) {
        
        //append headers
        this.headers = new HttpHeaders();
        this.headers.append("Content-Type", 'application/json');
        this.headers.append("Access-Control-Allow-Origin", "*");
        this.headers.append("Access-Control-Allow-Headers", "Origin, Authorization, Content-Type, Accept");
   }


   // Get Dentist
    getClinics(user_id = '23', clinic_id='1', token = this._cookieService.get("token")): Observable<any> {
        return this.http.get("http://localhost/jeeveanalytics/server/Practices/getPractices/23/", { headers: this.headers })
        .pipe(map((response: Response) => {
                        return response;
                    })
        );
    }

       // Delete Clinic
    deleteClinic(clinic_id, token = this._cookieService.get("token")): Observable<any> {
    const formData = new FormData();

    formData.append('id', clinic_id);
    formData.append('token', token);

        return this.http.post("http://localhost/jeeveanalytics/server/Practices/delete", formData)
        .pipe(map((response: Response) => {
                        return response;
                    })
        );
    }

    // Update Clinic
    updateClinic(clinic_id, value, column, token = this._cookieService.get("token")): Observable<any> {
    const formData = new FormData();

    formData.append('id', clinic_id);
    formData.append(column, value);
     formData.append('user_id', '23');
    formData.append('clinic_id', '1');

    formData.append('token', token);
    
        return this.http.post("http://localhost/jeeveanalytics/server/Practices/update/", formData)
        .pipe(map((response: Response) => {
                        return response;
                    })
        );
    }
}

